import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import jwtDecode from "jwt-decode";
import jpg1 from "@/assets/image/user1.jpg";
import jpg2 from "@/assets/image/user2.jpg";
import jpg3 from "@/assets/image/user3.jpg";
import HomeColor from "@/assets/tabBar/HomeIconColor.jpg";
import HomeGrey from "@/assets/tabBar/HomeIconGrey.jpg";
export default {
  setup() {
    const icon = {
      active: HomeColor,
      inactive: HomeGrey
    };
    const images = [jpg1, jpg2, jpg3];
    const calculateIconSize = 0.08 * window.innerWidth;
    const imageUrl = ref("");
    const name = ref("");
    const show = ref(false);
    const show_1 = ref(true);
    const desc = ref("");
    const credit = ref("");
    const active = ref(4);
    const router = useRouter();
    const token = localStorage.getItem("jwtToken"); // 从localStorage获取JWT令牌
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    let userId = "";
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        const decodedToken = jwtDecode(token);
        // 从解码后的令牌中获取特定的数据
        userId = decodedToken.id; // 从令牌中获取用户ID

        console.log(response.data.data);
        fetchDataAndFillForm();
        // 在这里处理登录令牌接口的响应
        // 如果需要执行一些特定的操作，可以在这里添加代码
      }).catch(error => {
        // 删除本地存储的 token
        localStorage.removeItem("jwtToken");
        router.replace("/login");
        console.error("请求loginToken接口失败", error);
      });
    };
    const fetchDataAndFillForm = async () => {
      try {
        const response = await axios.get(`/api/selectUser?id=${userId}`, {
          headers
        });
        const data = response.data.data; // 假设服务器返回的数据是一个包含上述字段的对象
        imageUrl.value = data.userImage;
        name.value = data.name;
        desc.value = data.userText;
        credit.value = data.credit;
      } catch (error) {
        console.error("获取数据失败", error);
      }
      show_1.value = false;
    };
    const copyToClipboard = () => {
      const generateShareLink = () => {
        return `/api/#/`;
      };
      const shareLink = generateShareLink();
      const tempInput = document.createElement("input");
      tempInput.value = shareLink;
      document.body.appendChild(tempInput);
      tempInput.select();
      document.execCommand("copy");
      document.body.removeChild(tempInput);
      show.value = true;
    };
    const toReplace = () => {
      window.location.href = "https://mp.weixin.qq.com/s/CEKa34igZ38DBT3n8Yowtg";
    };
    const requireItem = () => {
      router.replace("/Setting");
    };
    const linkUser = () => {
      router.replace(`/UserDisplay/${userId}`);
    };
    // 在组件加载完成后自动触发数据加载和填充
    onMounted(() => {
      fetchLoginToken();
    });
    const logout = () => {
      // 删除本地存储的 token
      localStorage.removeItem("jwtToken");

      // 使用 Vue Router 进行页面跳转到登录页面
      router.replace("/login");
    };
    return {
      calculateIconSize,
      toReplace,
      icon,
      show,
      show_1,
      active,
      name,
      desc,
      credit,
      imageUrl,
      images,
      requireItem,
      copyToClipboard,
      linkUser,
      logout
    };
  }
};